html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}

img, svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  padding: 0;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

/*
Доступно скрываю элементы
*/
.visually-hidden {
  position: fixed;
  transform: scale(0);
}

body {
  font-family: "Noto Sans";
  font-weight: 400;
  line-height: 21px;
  font-size: 16px;
  scroll-behavior: smooth;
}

.body__wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.container {
  max-width: 1280px;
  padding: 0 40px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  color: #FFFFFF;
  background-color: #080702;
  transition: background-color 0.3s;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1024px) {
  .header__container {
    padding-top: 12px;
    padding-bottom: 12px;
  }
}
@media (max-width: 1024px) {
  .header__container .header__logo__img {
    width: 86px;
    height: 25px;
  }
}
.header__container .header__block2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 23px;
}
.header__container .header__block2 .header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 31px;
  background-color: #080702;
}
@media (max-width: 1100px) {
  .header__container .header__block2 .header__nav {
    gap: 20px;
  }
}
@media (max-width: 1024px) {
  .header__container .header__block2 .header__nav {
    flex-direction: column;
    gap: 31px;
    visibility: hidden;
    position: absolute;
    top: 0;
    width: 100vw;
    max-width: 1024px;
    transition: transform 0.5s, visibility 0.5s;
    z-index: 10;
    padding-top: 66px;
    padding-bottom: 115px;
    margin-left: 71px;
  }
}
@media (max-width: 768px) {
  .header__container .header__block2 .header__nav {
    margin-left: 53px;
  }
}
.header__container .header__block2 .header__nav__item {
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  transition: 0.3s;
}
.header__container .header__block2 .header__nav__item:hover {
  color: #ebe4e4;
}
.header__container .header__block2 .header__nav__item:active {
  color: #ddd0d0;
}
.header__container .header__block2 .header__btn {
  font-family: Roboto;
  font-size: 20px;
  line-height: 23px;
  font-weight: 500;
  background-color: #29AAE3;
  border: 0;
  border-radius: 26px;
  transition: 0.3s;
  padding: 12px 18px;
}
@media (max-width: 1024px) {
  .header__container .header__block2 .header__btn {
    visibility: hidden;
    position: absolute;
    top: 345px;
    left: calc(50% + 91px);
    width: 183px;
    height: 47px;
    transition: transform 0.5s, visibility 0.5s;
    z-index: 10;
    margin-bottom: 44px;
  }
}
.header__container .header__block2 .header__btn:hover {
  background-color: #269ACD;
}
.header__container .header__block2 .header__btn:active {
  background-color: #9033F9;
}
.header__container .header__block2 .header__btn-language {
  position: relative;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  background-color: #080702;
  border: 0;
  transition: 0.3s;
}
@media (max-width: 1024px) {
  .header__container .header__block2 .header__btn-language {
    display: none;
  }
}
.header__container .header__block2 .header__btn-language:hover {
  color: #ebe4e4;
}
.header__container .header__block2 .header__btn-language:active {
  color: #ddd0d0;
}
.header__container .header__block2 .header__btn-language::after {
  display: block;
  position: absolute;
  top: 5px;
  right: -11px;
  content: "";
  width: 8px;
  height: 7px;
  background-image: url(/img/header-arrowsvg.svg);
}
.header__container .header__burger {
  display: none;
  position: relative;
  width: 32px;
  height: 22px;
  border: none;
  background-color: transparent;
  z-index: 11;
}
.header__container .header__burger span {
  position: absolute;
  width: 32px;
  height: 2px;
  background-color: #FFFFFF;
  transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
}
.header__container .header__burger span:nth-child(1) {
  transform: translateY(0);
}
.header__container .header__burger span:nth-child(2) {
  transform: translateY(11px);
}
.header__container .header__burger span:nth-child(3) {
  transform: translateY(22px);
}
@media (max-width: 1024px) {
  .header__container .header__burger {
    display: block;
  }
}

.open.header {
  transition: background-color 0.3s;
}
.open.header .header__burger span:nth-child(1) {
  transform: translateY(11px) rotate(45deg);
}
.open.header .header__burger span:nth-child(2) {
  opacity: 0;
}
.open.header .header__burger span:nth-child(3) {
  transform: translateY(11px) rotate(-45deg);
}
.open.header .header__nav {
  transform: translateX(-100%);
  visibility: visible;
}
.open.header .header__btn {
  transform: translateX(-100%);
  visibility: visible;
}

